<template>
    <div class="courseFile">
        <div>
            <el-select v-model="selectCourse" clearable filterable placeholder="请选择相应的课程">
                <el-option
                    v-for="item in myCourse"
                    :key="item.courseInfoId"
                    :label="item.courseName"
                    :value="item.courseName">
                </el-option>
            </el-select>
            <el-select v-model="selectClass" clearable filterable placeholder="请选择相应的班级">
                <el-option
                    v-for="item in myClass"
                    :key="item"
                    :label="item"
                    :value="item">
                </el-option>
            </el-select>
            <el-table
                ref="multipleTable"
                :data="realCourseFile"
                tooltip-effect="dark"
                style="width: 100%">
                <el-table-column
                    prop="courseName"
                    label="课程名称"
                    width="200"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="courseFolderName"
                    label="作业夹名称"
                    width="200"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="上课班级"
                    align="center"
                    prop="courseClass">
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center">
                    <template slot-scope="scope">
                        <el-button @click="downloadClamp(scope.row)" type="text" size="small">下载</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import {mapGetters ,mapActions} from 'vuex'
export default {
    data(){
        return{
            selectCourse:'',
            selectClass:'',
            realCourseFile:null
        }
    },
    computed:{
        ...mapGetters('teach',{
            myCourse:'getMyCourse',
            courseFile:'getCourseFile',
            myClass:'getMyClass'
        })
    },
    mounted(){
        this.requestFolder();
        this.realCourseFile=courseFile
    },
    methods:{
        ...mapActions('teach',["requestFolder","downloadCourseFolder"]),
        downloadClamp(content){
            this.downloadCourseFolder({content,that:this})
        }
    }
}
</script>

